.ChatInput {
    width: 95%;
    display: block;
    margin: auto;
  }
  
  .ChatInput input {
    padding: 10px;
    margin: 0;
    background-color: lightgrey;
    font-size: 16px;
    border: none;
    border-radius: 5px;
    border: 1px solid rgba(0,0,0,0.1);
    width: 98%;
    box-shadow: 0 5px 15px -5px rgba(0,0,0,.1);
  }
  